/* Competence Matrix */

.hr-matrix-controls {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 24px;
  flex-wrap: wrap;
}

.hr-matrix-controls__select {
  min-width: 280px;
}

.hr-matrix-controls__count {
  font-size: 0.875rem;
  color: #9ca3af;
}

.hr-matrix-wrapper {
  overflow-x: auto;
  border-radius: 12px;
  border: 1px solid hsl(var(--border));
  background: hsl(var(--card));
}

.hr-matrix-wrapper .hr-competence-table {
  min-width: 800px;
}

.hr-matrix-wrapper .hr-competence-table th {
  position: sticky;
  top: 0;
  z-index: 10;
  background: hsl(var(--surface-2));
}

.hr-matrix-wrapper .hr-competence-table th:first-child {
  position: sticky;
  left: 0;
  z-index: 20;
  background: hsl(var(--surface-2));
}

.hr-matrix-wrapper .hr-competence-table td:first-child {
  position: sticky;
  left: 0;
  z-index: 5;
  background: hsl(var(--card));
}

.hr-matrix-wrapper .hr-competence-table tbody tr:nth-child(even) td:first-child {
  background: hsl(var(--surface-2));
}

.hr-matrix-wrapper .hr-competence-table tbody tr:hover td:first-child {
  background: hsl(var(--accent));
}

.hr-matrix-wrapper .hr-competence-table td {
  text-align: center;
  vertical-align: middle;
}

.hr-matrix-cell {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
}

.hr-matrix-cell__level {
  font-size: 0.85rem;
  font-weight: 600;
  color: hsl(var(--foreground));
}

.hr-matrix-cell__levels {
  display: flex;
  align-items: center;
  gap: 2px;
  font-size: 0.8rem;
  color: hsl(var(--muted-foreground));
}

.hr-matrix-cell__levels strong {
  font-weight: 600;
  color: hsl(var(--foreground));
}

.hr-toggle {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  border-radius: 6px;
  border: 1px solid var(--hr-border);
  background: var(--hr-bg-secondary);
  cursor: pointer;
  transition: all 0.15s ease;
}

.hr-toggle--on {
  background: rgba(34, 197, 94, 0.2);
  border-color: rgba(34, 197, 94, 0.5);
  color: rgb(34, 197, 94);
}

.hr-toggle--off {
  background: rgba(239, 68, 68, 0.1);
  border-color: rgba(239, 68, 68, 0.3);
  color: rgba(239, 68, 68, 0.7);
}

.hr-toggle:hover {
  transform: scale(1.05);
}

